//grid 12 clm
@mixin rwd-min(){
  $v:8.33%;
  .clm-1-min {
    width: $v;
  }
  .clm-2-min {
    width: $v*2;
  }
  .clm-3-min {
    width: $v*3;
  }
  .clm-4-min {
    width: $v*4;
  }
  .clm-5-min {
    width: $v*5;
  }
  .clm-6-min {
    width: $v*6;
  }
  .clm-7-min {
