@import '_inall';
//Colors
$blue-dark:#1EAA8D;
$blue-light:#1BBC9B;
$social-facebook:#1952A9;
$social-twitter:#26D4F9;
$social-pinterest:#FB4343;
$social-google:#2D3E50;
$social-dribbble:#EB477C;
$social-t:#54809D;
$social-vimeo:#33A9EF;
$social-rss:#FF8A00;
$footer1:#1A1C28;
$footer2:#222534;
//Header
.header-middle {
  color: $c18;
  bottom: 25%;
  width: 100%;
  padding: 0 $ms2;
  transform: scale(0.6);
  opacity: 0;
  animation: boom .7s 1s forwards;
}
header {
  min-height: 670px;
}
.header-btn {
  color: $c18;
}
.header-btn-second {
  background: $blue-light;
  transition: all .5s;
}
.header-btn-first {
  border: 2px solid $c18;
  transition: all .5s;
}
.header-btn-second:hover {
  background: $blue-dark;
}
.header-btn-first:hover {
  background: $blue-dark;
  border-color: transparent;
}
@media screen and (min-width:480px) {
  .header-middle {
    bottom: 35%;
  }
  header {
    height: 550px;
  }
  .header-btn-second {
    margin-left: $ms2;
  }
}
//Menu
nav {
  box-shadow: 0px 3px 10px $c12;
  background: $c20;
}
.menu-home-anchor,
.menu-search {
  color: $blue-dark;
  font-size: $ms1;
  text-align: center;
}
.menu-home-anchor {
  font-size: $ms3;
  padding: $ms-2;
}
.menu-anchor {
  color: $c5;
  padding: $ms0;
}
.humb-child {
  span {
    background: $blue-dark;
  }
}
.li_hover {
  transition: all .3s;
  border-bottom: 3px solid transparent;
}
.li_hover:hover {
  border-bottom-color: $blue-dark;
}
@media screen and (min-width:1024px) {
  .menu-home-anchor {
    color: $c3;
  }
}
//4features
.four-features-span-outline {
  z-index: 10;
  border-radius: 100%;
  height: 60px;
  width: 60px;
  border: 2px solid $blue-light;
  margin-top: -30px;
  background: white;
  span {
    border-radius: 100%;
    border: 1px solid $blue-light;
    height: 50px;
    width: 50px;
    font-size: 20px;
    margin-left: 3px;
    margin-top: 3px;
  }
}
.four-features-icon {
  margin-top: 15px;
  margin-left: -8px;
  color: $blue-dark;
}
.four-features-item {
  border: .5px solid $blue-dark;
  padding: 0 $ms-3;
}
//about
.about-anchor {
  color: $c5;
}
.about-img {
  width: 95%;
  margin-top: $ms4;
}
//capability
.capability {
  color: $c18;
  padding-top: $ms5;
  padding-bottom: $ms6;
}
.capability-round {
  border-radius: 100%;
  $size: 170px;
  $inner-size: 80px;
  height: $size;
  width: $size;
  background-color: $c19;
  margin: 0 auto;
  box-shadow: 0 0 3px 5px $blue-light;
  padding-top: ($size/2)-($inner-size/2);
  span {
    text-align: center;
    color: $c3;
    font-size: $ms1;
    height: $inner-size;
    width: $inner-size;
    border-radius: 100%;
    margin-left: auto;
    margin-right: auto;
    background: $c18;
    padding-top: ($inner-size/2-5px)-($ms1/2);
  }
}
//Services
.icon-wrapper {
  margin: $ms0 auto;
  font-size: $ms3;
  margin-left: -12px;
}
//client
.client {
  background: $c18;
  padding-bottom: $ms4;
}
.client-txt {
  background: $c19;
  padding: $ms2 $ms-5;
  border-radius: 7px;
  transition: all 1s;
  transform: translateX(-100%);
  opacity: 0;
}
.client-img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}
.client-name {
  font-weight: bold;
}
@keyframes red {
  to {
    opacity: 0.3;
  }
}
@media screen and (min-width:480px) {
  .client-txt {
    padding: $ms2 $ms5;
  }
}
//social big
.social-big {
  margin-bottom: -1px;
}
.social-big-icon-wrapper {
  width: 50%;
  font-size: $ms5;
  padding: $ms5;
  color: $c19;
  i {
    transition: all .5s;
    margin-left: -12px;
  }
}
.facebook {
  background: $social-facebook;
}
.twitter {
  background: $social-twitter;
}
.pinterest {
  background: $social-pinterest;
}
.gplus {
  background: $social-google;
}
.dribbble {
  background: $social-dribbble;
}
.tumblr {
  background: $social-t;
}
.vimeo {
  background: $social-vimeo;
}
.rss {
  background: $social-rss;
}
.social-big-icon-wrapper:hover {
  i {
    transform: translateX(-7px) scale(1.5);
  }
}
//features
.features {
  color: $c19;
}
.features-phone {
  width: 250px;
  background: $c18;
  height: 500px;
  border-radius: 32px;
}
.features-phone-wrapper {
  padding-top: $ms-2;
}
.features-icon-wrapper-left,
.features-icon-wrapper-right {
  height: 40px;
  width: 40px;
  background: $blue-light;
  color: $c19;
  padding-top: 3px;
  font-size: $ms2;
  border-radius: 100%;
  padding-left: 4px;
}
.features-icon-wrapper-left {
  margin-right: $ms-4;
}
.features-icon-wrapper-right {
  margin-left: $ms-4;
}
.features-icon-fix {
  transform: translateX(-13px);
}
.features-left {
  text-align: left;
}
.features-right {
  text-align: right;
}
//txtlink
.txtlink-wrapper {
  border-left: 7px solid $blue-dark;
  padding-left: $ms1;
}
.txtlink-btn {
  color: $c19;
  background: $blue-light;
}
.txtlink-btn:hover {
  color: $blue-light;
  background: $c19;
}
@media screen and (min-width:1024px) {
  .txtlink-wrapper {
    border-left-width: 9px;
    padding-left: $ms3;
  }
  .txtlink-btn {
    margin-top: $ms5;
  }
}
//presentation
.pres-icon {
  height: 30px;
  width: 30px;
  background: $blue-light;
  color: $c19;
  padding-top: 3px;
  font-size: $ms0;
  border-radius: 100%;
  padding-left: 4px;
  margin-right: $ms-5;
}
.pres-h {
  margin-left: $ms2;
}
.pres-img {
  margin-top: $ms11*1.2;
}
.pres-btn {
  color: $blue-dark;
  border: 2px solid $blue-dark;
  border-radius: 7px;
}
.pres-btn:hover {
  color: white;
  border: 2px solid $blue-dark;
  border-radius: 7px;
  background: $blue-light;
}
//posts
.posts-img {
  width: 85px;
  height: 85px;
  margin-top: $ms4;
}
.posts-txt {
  width: 65%;
  padding: $ms-1;
}
.posts-metadata {
  margin: 5px;
}
.posts-icon {
  margin-right: $ms-5;
  color: $blue-dark;
}
@media screen and (min-width:480px) {
  .posts-img {
    width: 134px;
    height: 135px;
  }
}
@media screen and (min-width:768px) {
  .posts-img {
    width: 150px;
    height: 150px;
    margin-top: $ms3;
    margin-left: $ms5;
  }
}
@media screen and (min-width:1024px) {
  .posts-img {
    width: 100px;
    height: 100px;
    margin-top: $ms4;
    margin-left: 0;
  }
}
//contact
.contact {
  color: $c16;
}
.contact-icons {
  margin-right: $ms1;
  margin-top: $ms1;
  font-size: $ms1;
  color: $c16;
  transition: all .1s;
}
.contact-icons:hover {
  transform: scale(2);
}
.contact-form {
  border: 1px solid $c12;
  background: transparent;
  color: $c18;
  border-radius: 4px;
  outline: none;
  padding: $ms-2;
  margin-bottom: $ms0;
  width: 90%;
  font-size: $ms1;
}
.contact-textarea {
  height: 120px;
}
.contact-submit {
  background: $blue-dark;
  color: $c19;
  font-weight: bold;
  text-transform: uppercase;
  width: 90%;
  border: none;
  padding: $ms0;
}
.contact-submit:hover {
  background: $blue-light;
  color: $c20;
}
.person-img {
  width: 150px;
  height: 150px;
  margin-top: $ms3;
  margin-right: auto;
  margin-left: auto;
  border-radius: 3px;
  border: 2px solid $c17;
  overflow: hidden;
}
.person-txt {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}
.contact-name {
  margin-top: $ms1;
  font-size: $ms1;
  font-weight: bold;
}
.contact-job {
  margin-top: 5px;
}
@media screen and (min-width:768px) {
  .person-img {
    float: left;
  }
  .person-txt {
    width: 70%;
    float: left;
    text-align: left;
    padding-left: $ms2;
  }
}
@media screen and (min-width:1024px) {
  .person-img {
    width: 140px;
  }
  .person-txt {
    width: 60%;
  }
}
@media screen and (min-width:1270px) {
  .person-img {
    width: 150px;
  }
  .person-txt {
    width: 65%;
  }
}
//footer
.logo {
  width: 120px;
  height: 60px;
  background-color: aqua;
}
.footer {
  background-color: $footer1;
  color: $c17;
}
.footer-h {
  border-left: 7px solid $blue-light;
  padding-left: $ms-1;
}
.footer-input {
  padding: 15px $ms-3;
  border: 1px solid $c16;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background: transparent;
  margin: 0;
  font-size: $ms1;
  color: $c17;
  width: 72%;
}
.footer-input-btn {
  background: $blue-dark;
  border: none;
  padding: 16px $ms0;
  font-weight: bold;
  font-size: $ms1;
  transition: all .2s;
}
.footer-input-btn:hover {
  background: $blue-light;
}
.footer-item {
  transition: all .2s;
  border-bottom: 1px solid rgba(255, 255, 255, .2);
}
.footer-item:hover {
  border-bottom: 1px solid rgba(255, 255, 255, .8);
}
.footer-post-img {
  width: 70px;
  height: 70px;
  background: $blue-dark;
  margin-top: $ms1;
}
.footer-post-txt {
  width: 70%;
  padding-left: $ms-3;
}
.footer-link {
  color: $blue-light;
  font-weight: bold;
}
.footer-img-collection {
  margin: 2px auto;
}
.gallery-img {
  width: 65px;
  height: 65px;
  background: $blue-light;
  margin-left: 4px;
  transition: all .2s;
}
.gallery-img:hover {
  transform: scale(1.1);
}
@media screen and (min-width:480px) {
  .footer-post-txt {
    width: 60%;
    padding-left: $ms-1;
  }
}
@media screen and (min-width:768px) {
  .footer-input {
    width: 400px;
  }
}
@media screen and (min-width:1024px) {
  .footer-input {
    width: 72%;
  }
}
//last footer
.last-footer {
  background: $footer2;
}
.last-footer-copyright,
.last-footer-links {
  padding-top: $ms1;
  padding-bottom: $ms1;
  color: $c16;
}
.last-footer-copyright {
  text-align: center;
}
.last-footer-span {
  text-align: center;
}
.last-footer-links {
  border-top: 3px solid rgba( 0, 249, 99, 0);
  transition: all .3s;
  padding-left: $ms0;
}
.last-footer-links:hover {
  border-top-color: rgba( 0, 249, 99, 0.7);
}
@media screen and (min-width:1280px) {
  .last-footer-copyright {
    text-align: left;
  }
}
//Bk
@media screen and (min-width:320px) {}
@media screen and (min-width:360px) {}
@media screen and (min-width:375px) {}
@media screen and (min-width:480px) {}
@media screen and (min-width:768px) {
  @include rwd-min();
}
@media screen and (min-width:1024px) {
  @include rwd();
}
